@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap);
iframe{display:none}html{overflow-x:hidden}*{box-sizing:border-box;margin:0}body{font-family:"Quicksand",sans-serif;background:#2a2626;overflow-x:hidden}.background{position:fixed;top:0;left:0;background-size:cover;background-position:center;height:100vh;width:100vw;z-index:-1}.container{display:grid;align-content:baseline;grid-gap:20px;gap:20px;min-height:100vh;padding:16px 24px;background-color:rgba(0,0,0,.65)}@media screen and (min-width: 400px){.container{padding:10px 35px}}
.item{display:grid;justify-content:start}@media screen and (min-width: 400px){.item{grid-auto-flow:column;grid-template-columns:none}}.item__title{color:#fff;display:inline-block;font-size:20px;text-transform:uppercase;text-decoration:none}@media screen and (min-width: 400px){.item__title{font-size:40px;margin-left:10px}}@media screen and (min-width: 1024px){.item__title{font-size:70px}}.item__title:hover{font-style:italic}.block{display:grid;grid-gap:8px;gap:8px}.skills{display:grid;justify-content:start;display:grid;grid-template-columns:auto auto;grid-gap:5px;gap:5px}@media screen and (min-width: 400px){.skills{grid-auto-flow:column;grid-template-columns:none}}.skill{display:inline-block;width:100px;border-radius:3px;text-transform:uppercase;text-align:center;color:#fff}.skill--black{background:#000}.skill--green{background:green}.skill--orange{background:orange}.skill--red{background:rgba(203,122,122,.63)}.skill--blue{background:rgba(49,129,247,.63)}.skill--violet{background:rgba(127,17,224,.63)}.links{display:grid;grid-auto-flow:column;justify-self:flex-start;align-content:center;grid-gap:5px;gap:5px}@media screen and (min-width: 400px){.links{grid-auto-flow:row}}.link{position:relative;display:grid;justify-content:center;align-items:center;width:30px;height:30px}.link--demo{background-color:#b50e0e}.link--demo::before{content:"";position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:0;height:0;border-top:5px solid rgba(0,0,0,0);border-bottom:5px solid rgba(0,0,0,0);border-right:10px solid #fff}.link--gh{background-color:orange}.link--gh::before{content:"";position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:100%;height:100%;background-size:cover;background-image:url(data:image/png;base64,)}
